#edu-table {
  width: 60%;
  margin: 0 auto;

  text-align: center;
  table-layout: fixed;
}

.edu-type {
  font-size: 5vmin;
  margin: 0;
}

.edu-name {
  font-size: 3.5vmin;
  margin: 0;

  color: #666;
}

.edu-result {
  font-size: 6vmin;
  margin: 0;
}

.edu-cell {
  vertical-align: bottom;
  padding-left: 10px;
  padding-right: 10px;
}

.edu-cell-bottom {
  vertical-align: top;
}



